import {
  Component,
  ChangeDetectionStrategy,
  ViewEncapsulation,
  Input
} from '@angular/core';

import {VdlIconRegistry} from '../icon/icon-registry';

@Component({
  moduleId: module.id,
  selector: 'vdl-popover-predefined',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: 'popover-predefined.html',
  styleUrls: ['popover-predefined.css'],
  viewProviders: [VdlIconRegistry],
})
export class VdlPopoverPredefined {
  @Input() popoverTitle: string;
  @Input() popoverPlacement: 'auto' | 'top' | 'bottom' | 'left' | 'right' = 'auto';
  @Input() popoverType: 'info' | 'error' | 'custom';
  @Input() popoverIcon: string;

  constructor(vdlIconRegistry: VdlIconRegistry) {
    vdlIconRegistry.registerFontClassAlias('fontawesome', 'fa');
  }
}
